	<div>
		<b-row class="mx-auto">
			<div class="col-lg-3"></div>
			<b-card class="text-center col-lg-2 mr-2 col-lg-offest-6">
				<b-avatar class="mb-1" :variant="`light-primary`" size="45">
					<feather-icon size="21" icon="DollarSignIcon" />
				</b-avatar>
				<div class="truncate">
					<h2 class="mb-25 font-weight-bolder">
						{{ (userInfo.balance / 100).toFixed(2) }}
					</h2>
					<span>Èí±ÂåÖ‰ΩôÈ¢ù</span>
				</div>
			</b-card>
			<b-card class="card-congratulation-medal col-lg-4">
				<h5>Êé®Âπø‰Ω£Èáë üéâ</h5>
				<b-card-text class="font-small-3">
					ÊÇ®ÂΩìÂâçÂèØÁî®ÁöÑÊé®Âπø‰Ω£Èáë
				</b-card-text>
				<h3 class="mb-75">
					<b-link>{{ (userInfo.commission_balance / 100).toFixed(2) }}</b-link>
				</h3>
				<b-button variant="primary" class="mr-1">
					ÂàíËΩ¨
				</b-button>
				<b-button v-show="userInfo.commission_balance / 100 > 100" variant="primary">
					‰Ω£ÈáëÊèêÁé∞
				</b-button>
				<b-img :src="require('@/assets/images/illustration/badge.svg')" class="congratulation-medal" alt="Medal Pic" />
			</b-card>
		</b-row>
		<b-card>
			<b-card-header>
				<div class="d-flex align-items-center">
					<b-card-title class="ml-25">
						ËÆ¢ÂçïÂàóË°®
					</b-card-title>
				</div>
			</b-card-header>
			<b-table responsive :items="orders" :fields="fields" class="mb-0">
				<template #cell(status)="data">
					{{ statusId2Txt(data.value) }}
				</template>
				<template #cell(caozuo)="data">
					<b-button v-show="data.item.status == 0" :disabled="isPosting" @click="cancelOrder(data.item.trade_no)" variant="flat-primary">
						ÂÖ≥Èó≠
					</b-button>
				</template>
				<template #cell(created_at)="data">
					{{ data.value | formatDate }}
				</template>
				<template #cell(updated_at)="data">
					{{ data.value | formatDate }}
				</template>
				<template #cell(trade_no)="data">
					<b-button @click="GotoOrder(data.item.trade_no)" variant="flat-primary"> #{{ data.value }} </b-button>
				</template>
				<template #cell(cycle)="data">
					{{ data.item.plan.name + ' - ' + typeName2TypeTxt(data.value) }}
				</template>
				<template #cell(total_amount)="data"> Ôø•{{ data.value / 100 }} </template>
				<template #cell()="data">
