	</div>
</template>

<script>
import { BCard, BCardText, BLink, BButton, BImg, BAvatar, BRow, BTable, BBadge, BCardHeader, BCardTitle } from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
import { formatDate } from '@/libs/date'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	components: {
		BCard,
		BCardText,
		BLink,
		BImg,
		BButton,
		BAvatar,
		BRow,
		BTable,
		BBadge,
		BCardHeader,
		BCardTitle,
		ToastificationContent
	},
	directives: {
		Ripple
	},
	filters: {
		formatDate(time) {
			time = time * 1000
			var date = new Date(time)
			return formatDate(date, 'yyyy-MM-dd hh:mm')
		}
	},
	data() {
		return {
			isPosting: false,
			userInfo: { balance: 0, commission_balance: 0 },
			orders: [],
			fields: [
				{ key: 'trade_no', label: '# ËÆ¢ÂçïÂè∑' },
				{ key: 'cycle', label: 'È°πÁõÆ' },
				{ key: 'total_amount', label: 'ÈáëÈ¢ù' },
				{ key: 'status', label: 'Áä∂ÊÄÅ' },
				{ key: 'created_at', label: 'ÂàõÂª∫Êó∂Èó¥' },
				{ key: 'updated_at', label: 'Êõ¥Êñ∞Êó∂Èó¥' }
			]
		}
	},
	mounted() {
		this.getUserInfo()
		this.fetchOrders()
	},
	methods: {
		showToast(variant, position, title, text) {
			this.$toast(
				{
					component: ToastificationContent,
					props: {
						title: title,
						icon: 'InfoIcon',
						text: text,
						variant
					}
				},
				{
					position
				}
			)
		},
		GotoOrder(id) {
			this.$router.push({ name: 'payorder', params: { id: id } })
		},
		cancelOrder(order_id) {
			let data = {
				trade_no: order_id
			}
			this.isPosting = true
			this.$http({
				url: '/api/v1/user/order/cancel',
				method: 'POST',
				data: data
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						this.showToast('success', 'top-center', 'ÂèñÊ∂àÊàêÂäü', 'ÂèñÊ∂àËÆ¢ÂçïÊàêÂäü')
						this.fetchOrders()
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response && err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		},
		getUserInfo() {
			this.isPosting = true

			this.$http({
				url: '/api/v1/user/info',
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.userInfo = res.data
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		},
		statusId2Txt(statusId) {
			switch (statusId) {
				case 0:
					return 'ÂæÖÊîØ‰ªò'
				case 1:
					return 'ÂºÄÈÄö‰∏≠'
				case 2:
					return 'Â∑≤ÂèñÊ∂à'
				case 3:
					return 'Â∑≤ÂÆåÊàê'
				case 4:
					return 'Â∑≤ÊäòÊäµ'
			}
		},
		typeName2TypeTxt(typeId) {
			switch (typeId) {
				case 'month_price':
					return 'Êúà‰ªò'
				case 'quarter_price':
					return 'Â≠£‰ªò'
				case 'half_year_price':
					return 'ÂçäÂπ¥‰ªò'
				case 'year_price':
					return 'Âπ¥‰ªò'
				case 'onetime_price':
					return 'ÊåâÈáè'
				case 'two_year_price':
					return '‰∏§Âπ¥‰ªò'
				case 'three_year_price':
					return '‰∏âÂπ¥‰ªò'
				case 'reset_price':
					return 'ÊµÅÈáèÈáçÁΩÆÂåÖ'
				default:
					return 'Êú™Áü•'
			}
		},
		fetchOrders() {
			this.$http({
				url: '/api/v1/user/order/fetch',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						res.forEach((item, index, arr) => {
							if (item.status == 0 && this.fields.length == 6) {
								this.fields.push({ key: 'caozuo', label: 'Êìç‰Ωú' })
								return
							}
						})
						this.orders = res
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		}
	}
}
</script>
